import {useContext} from "react";
import NavigationBar from "../components/NavigationBar";
import ContentContainer from "../components/ContentContainer";
import Footer from "../components/Footer";
import TeamMemberCard from "../components/TeamMemberCard";
import ContactUsForm from "../components/ContactUsForm";
import {Typography} from "@mui/material";
import {GlobalStateContext} from "../state/GlobalStateContext";
import {Box} from "@mui/system";
import useWindowDimensions from "../hooks/useWindowDimensions";

const members = [
    {
        name: "Edrin Hansen",
        avatar: "Edrin2.jpg",
        text: "Edrin on meeskonna mootor, hoiab kigel ktt pulsil ja veendub, et kigil oleks asjad igeks ajaks" +
            " tehtud. Edrini roll on organiseerida meie tegevusi ning jagada tkorraldusi. Ta on vga tpne ja " +
            "entusiastlik ning juab ksi ra teha mitme mehe t. Tkojas oskab anda phjaliku ja tarka nu " +
            "akende ja uste restaureerimisel ning saviehituse ja krohvi teemadel.",
    },
    {
        name: "Kermo Aasme",
        avatar: "Kermo2.jpg",
        text: "Kermo hoolitseb terava pilguga, meie tkoja rahaasjade ning meeleolu eest. Temal jutt jookseb ja" +
            " ega see nii pea otsa ei saa. Meie seast on ta kige pikema ja suurema tkogemusega kiviehituse ja" +
            " viimistlustde osas. Tema poole vib alati oma ehitusalase ksimuse ja murega prduda ja" +
            " saad kindla ning phjaliku vastuse."
    },
    {
        name: "Joosep Mattis Eriste",
        avatar: "Joosep.jpg",
        text: "Joosep on kll lbinisti palgimees, kuid hea suhtleja. Tkojas saab Joosepi kest vastuse " +
            "kikidele ksimustele seoses palkehitusega ning metsandusega. Joosep on sammuti osav muusik " +
            "ning jagab ka oma teadmisi noortele, keda ta pilli petab mngima."
    },
    {
        name: "Raimond Russi",
        avatar: "Raimond2.jpg",
        text: "Raimond on meie sotsiaalmeedia haldur, kes vajadusel remondib ra ka teie auto ning vahetab majal " +
            "mdanenud palgi. Raimond on igati mitmeklgne ja kirgas noormees, kellel on palju hobisid (autod- " +
            "eriti Saab, kaitseliit, videomngud ja reisimine). Kuid teise poole elust moodustavad erialased " +
            "tegemised, millest lviosa ajast kulub pingutele T VKA rahvusliku ehituse erialal ning aeg-ajalt " +
            "Norras tl kimine (palgivahetus)"
    }
];

members.sort(() => Math.random() - 0.5);

const Contact = () => {
    const {titleShort} = useContext(GlobalStateContext);
    document.title = titleShort + " | Kontakt";
    const {width} = useWindowDimensions();
    return (
        <>
            <NavigationBar/>
            <ContentContainer
                sx={{
                    display: "flex",
                    flexDirection: "column",
                    alignItems: "center",
                }}
            >
                <Typography
                    gutterBottom
                    variant="h4"
                    width={width > 696 ? "8em" : "auto"}
                    sx={{
                        marginTop: "2em",
                        marginBottom: "2em",
                        borderBottom: "2px solid #D9D9D9",
                        height: "1.5em",
                        textAlign: "center",
                    }}
                >
                    Meeskond
                </Typography>
                <Box sx={{
                    display: "flex",
                    gap: "30px",
                    flexWrap: "wrap",
                    justifyContent: "center",
                    margin: "0 25px 100px 25px"
                }}>
                    {members.map((member) => (
                        <TeamMemberCard
                            key={member.name}
                            name={member.name}
                            text={member.text}
                            avatar={member.avatar}
                        />
                    ))}
                </Box>
                {width > 500 ?
                    <Typography
                        gutterBottom
                        variant="h4"
                        width={width > 696 ? "14.5em" : "auto"}
                        sx={{
                            marginTop: "3em",
                            marginBottom: "2em",
                            borderBottom: "2px solid #D9D9D9",
                            height: "1.5em",
                            textAlign: "center",
                        }}
                    >
                        Vta meiega hendust
                    </Typography>
                    :
                    <>
                        <Typography
                            gutterBottom
                            variant="h4"
                            sx={{
                                textAlign: "center",
                                margin: "0",
                                marginTop: "3em",
                            }}
                        >
                            Vta meiega
                        </Typography>
                        <Typography
                            gutterBottom
                            variant="h4"
                            sx={{
                                textAlign: "center",
                                borderBottom: "2px solid #D9D9D9",
                                marginBottom: "2em",
                                height: "1.5em",
                                width: "6em",
                            }}
                        >
                            hendust
                        </Typography>
                    </>
                }
                <ContactUsForm/>
            </ContentContainer>
            <Footer/>
        </>
    );
};

export default Contact;
